:root {

	--section-width: 1000px; 
	--navbar-height: 75px;

	--default-font: antipasto-pro;
	--default-background-color: #f0f8ff;
	
	--navbar-links-container-margin: 20px;
	--navbar-link-margin: 20px;
	--navbar-transition-duration: 0; /*0.69s*/
	--navbar-transition-burger-duration: 0.69s;
	--navbar-burger-stick-offset: 9px;

	--footer-link-size: 30px;

	--landing-splash-transition: all 1s;
	--landing-game-preview-transition: all 0.25s;

	--about-experience-banner-padding-bottom: 25%;
	--about-experience-transition: 0.5s all;

	--about-portrait-width: 300px;
	--about-portrait-height: 400px;

	--game-details-grid-gap: 20px;
}

@font-face {

	font-family: bebas-neue;
	src: url('./fonts/bebas-neue/bebas-neue.ttf');
}

@font-face {

	font-family: antipasto-pro;
	src: url('./fonts/antipasto-pro/antipasto-regular.otf');
	/*src: url('./fonts/antipasto-pro/antipasto-pro-light.ttf');*/
}

.switch-wrapper
{
  position: relative;
}

.switch-wrapper > div {
  
  position: absolute;
}

html {

	margin: 0;
}

body::-webkit-scrollbar {

	display: none;
}

body {

	position: relative;

	margin: 0;

	font-family: var(--default-font);
	font-size: 24px;

	background-color: var(--default-background-color);

	overflow-x: hidden;

	width: 100%;

	-ms-overflow-style: none;
	scrollbar-width: none;
}

/*********************************************
COMMON
**********************************************/

.page {
	
	width: 100vw;

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
}

.section {

	width: var(--section-width);
}

.section-header {

	font-family: bebas-neue;
	font-size: 3em;

	margin: 0 0 10px 0;
}

/*********************************************
NAVBAR
**********************************************/

.navbar {
	
	width: 100vw;

	position: fixed;
	top: 0;

	height: var(--navbar-height);
	
	background: rgba(0,0,0,0);
	color: white;

	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	
	transition-duration: var(--navbar-transition-duration);
	transition-property: all;

	border-bottom: none; /*1px solid black;*/
	box-shadow: none; /*0px -10px 15px 10px rgba(0,0,0,0.5);*/

	z-index: 5;
}

.navbar-logo-container {
	
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;

	flex-grow: 0; /* transitions 0 - 1*/ 
	opacity: 0; /* transitions 0 - 1*/ 

	height: 100%;
	width: 0px;

	overflow: hidden;

	transition-duration: var(--navbar-transition-duration);
	transition-timing-function: ease-in;
	transition-property: all;
}

.navbar-logo-container > a {
	
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;

	height: 80%;

	margin-left: calc(
		var(--navbar-links-container-margin) * 1.5
		);
}

.navbar-logo {

	height: 100%;
	width: auto;
}

.navbar-links-container {

	color: inherit;
	
	margin: 
		0px
		var(--navbar-links-container-margin)
		0px
		var(--navbar-links-container-margin);
	
	transition-duration: var(--navbar-transition-duration);
	transition-property: all;
}

.navbar-link {

	display: inline-block;

	color: inherit;
	text-decoration: none;
	
	margin: 
		0px
		var(--navbar-link-margin)
		0px
		var(--navbar-link-margin);
	
	transform: scale(1);

	transition-duration: 0.45s;
	transition-property: all;
}

.navbar-link:hover {
	
	transform: scale(1.25);
}

.navbar-burger-menu {

	position: absolute;
	
	margin: 0;

	right: 10px;
	top: 0;
	bottom: 0;

	height: var(--navbar-height);
	width: var(--navbar-height);

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;

	display: none;
	
	transition-duration: var(--navbar-transition-burger-duration);
	transition-property: all;
}

.navbar-burger-content {

	width: 100%;
	height: 100%;
}

.navbar-burger-sticks-container {

	width: 100%;
	height: 100%;

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.navbar-burger-stick {

	width: 60%;

	border-bottom-width: 3px;
	border-bottom-style: solid;
	border-bottom-color: white;

	margin: 3px 0 3px 0;
	
	transform: scale(1);

	transition: var(--navbar-transition-burger-duration) all;
}

.navbar-burger-top {
	
	transition: var(--navbar-transition-burger-duration) all;
}

.navbar-burger-middle {
	
	transition: var(--navbar-transition-burger-duration) all;
}

.navbar-burger-bottom {
	
	transition: var(--navbar-transition-burger-duration) all;
}

.navbar-burger-toggle {

	position: absolute;

	top: 0;
	bottom: 0;
	right: 0;
	left: 0;

	width: 100%;
	height: 100%;

	opacity: 0;

	z-index: 5;
}

.navbar-burger-links-container {
	
	width: auto;
	
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	justify-content: flex-start;

	margin: calc(var(--navbar-height) - 1px) 0 0 0;

	padding-left: 40px;
	
	background: var(--default-background-color);
	
	border-bottom-left-radius: 20px;

	position: fixed;
	top: 0;
	right: 0;

	z-index: 2;

	max-height: 0px;
	overflow: hidden;
	
	transition: var(--navbar-transition-burger-duration) all;
}

.navbar-burger-links-container .navbar-link {

	margin: 15px 30px 15px 0;

	font-size: 1.25em;
}

.navbar-burger-toggle:checked + 
.navbar-burger-content >
.navbar-burger-overlay {

	display: initial;
	opacity: 1;
}

.navbar-burger-toggle:checked + 
.navbar-burger-content >
.navbar-burger-sticks-container >
.navbar-burger-top {

	transform: translateY(var(--navbar-burger-stick-offset)) rotateZ(-225deg);
}

.navbar-burger-toggle:checked + 
.navbar-burger-content >
.navbar-burger-sticks-container >
.navbar-burger-middle {

	transform: rotateY(-450deg);
}

.navbar-burger-toggle:checked + 
.navbar-burger-content >
.navbar-burger-sticks-container >
.navbar-burger-bottom {

	transform: translateY(calc(var(--navbar-burger-stick-offset) * -1)) rotateZ(225deg);
}

/*********************************************
FOOTER
**********************************************/

.footer {

	width: 100vw;

	padding: 150px 0 75px 0;

	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
}

.footer-email {

	margin-bottom: 20px;
}

.footer-links {
	
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: flex-start;
}

.footer-link {

	width: var(--footer-link-size);
	height: var(--footer-link-size);

	margin: 0 5px 0 5px;

	border-radius: 50%;
	overflow: hidden;

	cursor: pointer;

	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;

	border: 2px solid black;
}

.footer-link-img {

	width: 70%;
	height: auto;
}

/*********************************************
LANDING PAGE
**********************************************/

.landing-page {}

.landing-splash-section {

	height: 100vh;
	width: 100vw;

	background-image: url("./images/game_banners/disclaimer.png");
	background-position: center;
	background-size: cover;

	margin-bottom: 40px;
}

.landing-black-overlay {
	
	height: 100%;
	width: 100%;
	
	background: rgba(0,0,0,0.75);
}

.landing-info-container {
	
	padding-top: calc(var(--navbar-height) * 0.5);

	height: 100%;
	width: 100%;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.landing-info-container > p {

	color: white;
}

@keyframes landing-info-logo-anim {

	0% {
		transform: translate(0px, 0px);
	}

	50% {
		transform: translate(0px, -20px);
	}

	100% {
		transform: translate(0px, 0px);
	}
}

.landing-info-logo {

	width: 240px;
	height: auto;

	margin-bottom: 20px;

	animation-name: landing-info-logo-anim;
	animation-duration: 3s;
	animation-iteration-count: infinite;
	animation-timing-function: ease-in-out;

	transition: var(--landing-splash-transition);
}

.landing-info-name {

	margin: -15px 0 0 0;

	font-family: bebas-neue;
	font-size: 4em;

	stroke: black;
	stroke-width: 1px;
	
	transition: var(--landing-splash-transition);
}

.landing-info-role {
	
	margin: -7px 0 0 0;

	font-size: 1.6em;
	
	transition: var(--landing-splash-transition);
}

.landing-games-section {

	padding: 40px 0 40px 0;

	display: flex;
	flex-direction: column;
	align-items: center;
}

.landing-games-gallery {

	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-auto-rows: 1fr;
	grid-gap: 15px;

	justify-content: center;
	align-items: center;

	width: 100%;
}

.landing-games-gallery::before {

	content: '';
	width: 0;
	padding-bottom: 100%;
	grid-row: 1/1;
	grid-column: 1/1;
}

.landing-games-gallery > *:first-child {
	
	grid-row: 1/1;
	grid-column: 1/1;
}

.landing-game-preview {
	
	width: 100%;
	height: 100%;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	position: relative;

	text-decoration: none;

	overflow: hidden;

	cursor: pointer;
}

.landing-game-preview:hover > .landing-game-preview-img {
	
	transform: scale(1.25);
}

.landing-game-preview:hover > .landing-game-name-container {
	
	opacity: 1;
}

.landing-game-preview-img {
	
	position: absolute;
	top: 0;

	height: 100%;
	width: auto;

	transition: var(--landing-game-preview-transition);

	transform: scale(1);
}

.landing-game-name-container {

	opacity: 0;

	width: 100%;
	height: 100%;

	background-color: rgba(0,0,0,0.25);

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	
	text-align: center;

	transition: var(--landing-game-preview-transition);

	z-index: 1;

	color: white;

	font-size: 1.25em;
}

.landing-games-details {

	margin: 0 0 40px 0;
}

.landing-games-details-link {

	color: dodgerblue;
	
	font-size: 1.125em;
	font-weight: bold;
}

/*********************************************
ABOUT PAGE
**********************************************/
.about-page {

	padding-top: var(--navbar-height);
}

.about-intro-section {

	margin-top: var(--navbar-height);

	display: flex;
	flex-direction: column;
	justify-content: flex-start;

	position: relative;
}

.about-section-header {

	
}

.about-intro-start {

	display: flex;
	flex-direction: row;
	align-items: flex-start;
	justify-content: space-between;
}

.about-intro-portrait-container {
	
	min-width: var(--about-portrait-width);
	max-width: var(--about-portrait-width);
	
	min-height: var(--about-portrait-height);
	max-height: var(--about-portrait-height);

	margin: 0 40px 0 0;

	position: relative;

	display: flex;
	flex-direction: column;
	align-items: center;

	border: 1px solid black;
	background: rgba(255, 106, 0, 0.5);

	overflow: hidden;
}

.about-intro-portrait {

	position: absolute;

	top: 0;

	height: 120%;
	width: 120%;

	background-image: url("./images/about/portrait.png");
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
}

.about-intro-description-container {
	
	white-space: pre-wrap;
}

.about-intro-description {
	
	white-space: pre-wrap;
	text-align: justify;
}

.about-intro-education-container {
	
	white-space: pre-wrap;
	text-align: justify;

	margin: 40px 0 40px 0;
}

.about-intro-gallery {
	
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 40px;
}

.about-intro-image-container {

	padding-bottom: 66%;

	position: relative;

	overflow: hidden;
}

.about-intro-image {

	width: 100%;
	height: auto;

	position: absolute;
	top: 0;
	bottom: 0;
}

.about-experience-section {

	margin-top: 80px;

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
}

.about-experience-container {
	
	width: 100%;
	position: relative;
}

.about-experience-container:nth-child(n+2) {
	
	margin-top: 40px;
}

.about-experience-banner {

	background: rgba(0,0,0,1);
	
	width: 100%;
	position: relative;

	padding-bottom: var(--about-experience-banner-padding-bottom);

	color: white;

	overflow: hidden;

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.about-experience-img {

	width: 100%;
	height: auto;

	top: -30%;

	position: absolute;

	opacity: 0.5;
	
	transform: scale(1);

	transition: var(--about-experience-transition);
}

@keyframes about-experience-arrow-animation {
	
	0% {
		transform: translate(0px, 0px);
	}

	25% {
		transform: translate(0px, -10px);
	}

	50% {
		transform: translate(0px, 0px);
	}
	
	100% {
		transform: translate(0px, 0px);
	}
}

.about-experience-arrow {

	position: absolute;
	padding: 10px;

	bottom: 0;

	font-weight: bold;

	transform: scale(1.5, 1);
	opacity: 0.5;
	
	transition: var(--about-experience-transition);
	/*
	animation-name: about-experience-arrow-animation;
	animation-duration: 6s;
	animation-iteration-count: infinite;
	*/
}

.about-experience-banner-contents {

	z-index: 1;

	position: absolute;
	top: 0;

	height: 100%;
	
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.about-experience-role {

	font-family: bebas-neue;
	font-size: 2em;
}

.about-experience-location {

	font-size: 1.25em;
}

.about-experience-toggle {

	width: 100%;
	height: 100%;

	position: absolute;
	margin: 0;

	top: 0;

	opacity: 0;

	z-index: 2;
}

.about-experience-details-container {

	overflow: hidden;
	max-height: 0;
	
	width: 100%;
	white-space: pre-wrap;
	text-wrap: normal;
	word-wrap: normal;
	text-align: center;

	padding: 0 0 0 0;

	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;

	opacity: 0;
	
	transition: var(--about-experience-transition);
}

.about-experience-details {

	width: 75%;
}

.about-experience-toggle:checked +
.about-experience-contents >
.about-experience-banner >
.about-experience-img {

	transform: scale(1.05)
}

.about-experience-toggle:checked +
.about-experience-contents >
.about-experience-banner >
.about-experience-arrow {
	
	transform: scale(1.5, -1);
}

.about-experience-toggle:checked + 
.about-experience-contents > 
.about-experience-details-container {

	max-height: 250px;

	opacity: 1;
}

/*********************************************
GAMES PAGE
**********************************************/

.games-page {

	padding-top: var(--navbar-height);
}

.games-gallery-section {

	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	
	position: relative;

	margin: 40px 0 40px 0;
	height: 200px;

	border-bottom: 1px solid black;
}

.games-gallery-item {

	width: 250px;
	height: 125px;

	position: absolute;
	
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;

	background: rgba(0,0,0,0.5);
	color: white;

	transition: all 0.5s;
}

.game-gallery-item-img {

	width: 100%;
	height: auto;
}

.game-details-section {
	
	display: grid;
	grid-gap: var(--game-details-grid-gap);
	grid-template-columns: 2fr 1fr;

	transition: 0.25s all;
}

.game-details.media {
	
}

.game-details-video-container {
	
	position: relative;
	padding-bottom: 56.25%;

	margin-bottom: var(--game-details-grid-gap);
}

.game-details-video {

	position: absolute;

	width: 100%;
	height: 100%;

	top: 50%;
	left: 50%;
	
	transform: translate(-50%, -50%);
}

.game-details-info {
	
	padding: 10px;
	background: rgba(0,0,0,0.125);

	display: flex;
	flex-direction: column;
	justify-content: flex-start;
}

.game-details-name {

	font-family: bebas-neue;
	font-size: 2em;

	width: 100%;
	text-wrap: normal;
	word-wrap: hyphenate;
}

.game-details-link-container {

	border-bottom: 1px solid black;

	padding-bottom: 20px;
	margin-bottom: 20px;
}

.game-details-link {

	padding: 5px 10px 5px 10px;
	
	font-size: 0.75em;
	text-decoration: none;

	border-radius: 5px;

	background: rgba(64, 255, 128, 1);
}

.game-details-description {

	white-space: pre-wrap;
}

.game-details-screenshots {
	
	display: grid;
	grid-gap: calc(var(--game-details-grid-gap) * 0.5);
	grid-template-columns: 1fr 1fr 1fr;
}

.game-details-screenshot-container {
	
	position: relative;
	padding-bottom: 56.25%;
}

.game-details-screenshot {

	position: absolute;

	height: 100%;
	width: auto;

	top: 50%;
	left: 50%;

	transform: translate(-50%, -50%);
}

/*********************************************
CONTACT PAGE
**********************************************/

.contact-page {
	
	padding-top: var(--navbar-height);
}

.contact-section {

	padding-top: var(--navbar-height);

	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
}

.contact-form {

	margin-top: 40px;

	width: 100%;

	display: grid;
	grid-gap: 40px;
	grid-template-areas:
		'cFir cLas'
		'cEma cEma'
		'cSbj cSbj'
		'cMes cMes'
		'cSub cSub';
}

.contact-field {

	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
}

.contact-field-error {

	color: red;
	margin: 0;

	width: 100%;
	text-align: left;
	
	max-height: 0px;

	transition: all 0.25s;
}

.contact-field-error-shown {
	
	max-height: 50px;
	opacity: 1;
}

.contact-field-error-hidden {
	
	max-height: 0px;
	opacity: 0;
}

.contact-label {

	width: 100%;

	margin-bottom: 10px;
}

.contact-input {
	
	width: 100%;
	height: 40px;

	font-family: antipasto-pro;
	font-size: 1.125em;
}

.contact-first-name {

	grid-area: cFir;
}

.contact-last-name {

	grid-area: cLas;
}

.contact-email {

	grid-area: cEma;
}

.contact-subject {

	grid-area: cSbj;
}

.contact-message {

	grid-area: cMes;
}

.contact-message-input {

	height: 300px;
}

.contact-submit-container {

	grid-area: cSub;
}

.contact-submit {
	
	margin-top: 20px;

	background: rgba(64, 128, 255, 1);
	color: white;

	width: 250px;
	height: 75px;

	border-radius: 10px;

	font-family: bebas-neue;
	font-size: 1.25em;

	display: flex;
	justify-content: center;
	align-items: center;
}

/*********************************************
Media Query: Max 1200px
**********************************************/

@media (max-width: 1200px) {

	:root {
		
		--section-width: 85%;
	}

}

/*********************************************
Media Query: Max 1024px
**********************************************/

@media (max-width: 1024px) {
	
	:root {
	
		--about-portrait-height: 300px;
	}

	.about-section-header {
	
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.about-intro-start {
	
		flex-direction: column;
		align-items: center;
		justify-content: flex-start;
	}

	.about-intro-portrait-container {
	
		margin: 0 0 40px 0;
	}
	
	.games-gallery-section {
	
		margin: 40px 0 20px 0;
	}

	.game-details-section {
	
		display: flex;
		flex-flow: column-reverse;
	}

	.game-details-info {
	
		display: flex;
		flex-direction: column;
		align-items: center;

		background: none;
	}

	.game-details-name {
	
		text-align: center;
		font-size: 2.5em;
	}

	.game-details-link-container {
	
		border-bottom: none;

		margin-top: 10px;
	}

	.game-details-screenshots {
		
		grid-gap: var(--game-details-grid-gap);
		grid-template-columns: 1fr 1fr;
	}
}

/*********************************************
Media Query: Max 768px
**********************************************/

@media (max-width: 768px) {

	.landing-info-logo {
	
		width: 200px;
	}

	.landing-info-name {
	
		font-size: 2.75em;
	}

	.landing-info-role {
	
		font-size: 1.25em;
	}

	.landing-games-gallery {
	
		grid-template-columns: 1fr 1fr;
	}
	
	.about-experience-role {
	
		font-size: 1.5em;
	}

	.about-experience-location {
	
		font-size: 1em;
	}

	.contact-form {
	
		grid-template-areas:
			'cFir'
			'cLas'
			'cEma'
			'cSbj'
			'cMes'
			'cSub';
	}
}  

/*********************************************
Media Query: Max 580px
**********************************************/

@media (max-width: 580px) {

	:root {
	
		--navbar-height: 60px;
	}

	body {
	
		font-size: 20px;
	}

	.navbar-links-container {
	
		display: none;
	}

	.navbar-burger-menu {
		
		display: flex;
	}
